export default {
    /**
     * [cookie]
     * @param {String} name    [cookie]
     * @param {String} val     [cookie]
     * @param {[Date]} expires []
     * @param {[String]} path    [cookie]
     */
    set:function(name,val,expires,path){
        // document.cookie = 'cartlist=1234;expires=' + now
        var cookieStr = name + '=' + val;

        // 
        if(expires){
            cookieStr += ';expires=' + expires.toUTCString();
        }

        // 
        if(path){
            cookieStr += ';path=' + path;
        }

        // 
        document.cookie = cookieStr;
    },
    get:function(name){
        // cookie
        var cookie = document.cookie;
        if(cookie.length === 0){
            return '';
        }

        // 
        cookie = cookie.split('; ');

        // cookiecookie
        var res = '';
        cookie.forEach(function(item){
            var arr = item.split('=');

            if(arr[0] === name){
                res = arr[1];
            }
        });

        return  res;
    },
    remove:function(name){
        // 
        var now = new Date();
        now.setDate(now.getDate()-100);

        // document.cookie = name +'=xxx;expires=' + now.toUTCString();
        Cookie.set(name,null,now);
    }
}


